html.color-theme-in-transition,
html.color-theme-in-transition *,
html.color-theme-in-transition *:before,
html.color-theme-in-transition *:after {
    transition: all 750ms !important;
    transition-delay: 1ms !important;
}

html {
    --background-color: #141e6e;
    --border: #fff;
    --primaryText: #fff;
    --topTabs: #131324;
    --fans: #e9f1f6;
    --fans_red: #fa00d4;
    --footerText: #eeeee7;
    --QR_icon-backgound: #791f25;
    --QR_icons: #beb391;
    --AppReloadsF: #084299;
    --AppReloadsS: #0a5579;
}

html[data-theme="night"] {
    --background-color: #030b3c;
    --border: #e91c4c;
    --primaryText: #6f79b7;
    --topTabs: #3b3b94;
    --fans: #4365b8;
    --fans_red: #cb21ad;
    --footerText: #8165cd;
    --QR_icon-backgound: #2c2488;
    --QR_icons: #60699b;
    --AppReloadsF: #09377e;
    --AppReloadsS: #051c58;
}


@media only screen and (min-device-width: 700px){
  .App {
    max-width:800px;
    max-height:480px;
    -webkit-user-select: none; /* Chrome/Safari */
  }
}
@media only screen and (min-device-width:800px) {
  .App {
    max-width: 800px;
    max-height: 480px;
    touch-action: none;
    overflow: hidden;
    -webkit-user-select: none; /* Chrome/Safari */
  }
}
@media only screen and(min-device-width:890px) {
  .App {
    max-width: 1200px;
    max-height: 580px;
    touch-action: none;
    overflow: hidden;
    -webkit-user-select: none; /* Chrome/Safari */
  }
}

body {
  background: var(--background-color);
  font: 14px "Century Gothic", Futura, sans-serif;
  text-align: center;
  margin: 0px;
}
.grid-container {
  display: grid;
  width:800px;
  grid-template-columns: 800px;
  grid-template-rows: 480px;
  grid-template-areas: "touchscreen";
  border: 0px solid var(--border);
  background-color: var(--background-color);
  -webkit-user-select: none; /* Chrome/Safari */
}
.touchscreen {
  display: grid;
  grid-template-columns: 800px;
  grid-template-rows: 45px 340px 90px;
  grid-template-areas: "menu" "main" "footer";
  grid-area: touchscreen;
  border: 0px solid var(--border);
  -webkit-user-select: none; /* Chrome/Safari */
}
.top-tab {
  display: grid;
  grid-template-columns: 200px 200px 200px 200px;
  grid-template-areas: "tab1" "tab2" "tab3" "tab4";
  border: 1px solid var(--topTabs);
}
.tab1{
  float: left;
  display: inline-block;
  padding: 1px;
  width: 200px;
  height: 45px;
  top: 0;
  left: -10px;
}
.tab2{
  color: #f8e16b;
}
.tab3{
  color: #52dd52;
}
.tab4{
  color: #e90de9;
  width: 200px;
  padding: 1px;
}

.main {
  display: grid;
  grid-template-columns: 320px 250px 220px;
  grid-template-areas: "outdoor" "indoor" "set_temp";
  border: 0px solid var(--border);
}

.outdoor {
  display: grid;
  margin-top: 25px;
  grid-template-columns: 320px;
  grid-template-rows: 30px 110px 26px 10px 90px 26px;
  grid-template-areas: "out_date" "out_time" "out_text" "out_temp_humid" "h_line" "status" "h_line";
  border: 0px solid var(--border);
}

.indoor {
  display: grid;
  margin-top: 25px;
  grid-template-columns: 250px ;
  grid-template-rows: 50px 170px 30px 200px;
  grid-template-areas: "indoor_label" "temp_indoor" "humidity" "windows";
  border: 1px solid var(--border);
}

.set_temp {
  display: grid;
  margin-top: 20px;
  grid-template-columns: 220px;
  grid-template-rows: 150px 80px 80px 80px 40px;
  grid-template-areas: "arrow_up" "temp_setting" "arrow_down" "fans" "fan_text";
  border: 0px solid #323eaa;
}

.footer {
  display: grid;
  grid-template-columns: 320px 220px 250px;
  grid-template-areas: "footer_left footer_center footer_right";
  grid-area: footer;
  border: 0px solid var(--border);
  color: var(--footerText);
  margin-top: 10px;
}
.config_room{
  margin-top: -55px;
  left: 30px;
  color: var(--footerText);
}
.motion_PIR{
  margin-top: 4px;
  color: var(--footerText);
}
.one_Wire_Temp{
  margin-top: 1px;
  color: var(--footerText);
}
.backlight  {
  margin-top: 1px;
}

.footer_left {
  grid-area: footer_left;
  border: 0px solid var(--border);
  margin-top: 35px;
}
.footer_center {
  grid-area: footer_center;
  border: 0px solid var(--border);
  margin-top: 30px;
}
.footer_right {
  grid-area: footer_right;
  border: 0px solid var(--border);
  text-align: center;
  margin-top: 50px;
}
.out_date {
  position: relative;
  right: 32px;
  font-size: 20px;
  color: var(--primaryText);
}
.out_time{
  position: relative;
  right: 30px;
  font-size: 46px;
  color: var(--primaryText);
}
.out_text{
  position: relative;
  right: 85px;
  font-size: 22px;
  color: var(--primaryText);
}
.h_line {
  position: relative;
  width:80%;
  left: 25px;
  height:2px;
  background: var(--primaryText);
  color: var(--primaryText);
}
.out_temp_humid{
  left: 10px;
  font-size: 22px;
  font-weight: bold;
  color: var(--primaryText);
}
.out_temp_humid_wind{
  margin-top: 10px;
  left: 10px;
  font-size: 17px;
  color: var(--primaryText);
}
.status{
  position: relative;
  right: 104px;
  font-size: 22px;
  color: var(--primaryText);
}

.appReloader {
    float: left;
    width:25px;
    height:25px;
    margin-top: 1px;
    padding-left: 10px;
}

.AppReload {fill: var(--AppReloadsF);}
.AppReload {stroke: var(--AppReloadsS);}

.arrow_up{
    margin-top: 2px;
    margin-bottom: 10px;
    color: var(--primaryText);
}
.arrow_down{
    margin-top: 0px;
    margin-bottom: 5px;
    color: var(--primaryText);
}
.temp_setter{
    font-size: 50px;
    font-weight: bold;
    margin-top: 8px;
    margin-bottom: 8px;
    color: var(--primaryText);
}
.temp_small{
    font-size: 30px;
    color: var(--primaryText);
}

img {
    border-radius: 50%;
    width: 75px;
    height: 75px;
}

.thermo-indoor-grid-C {
    display: grid;
    grid-template-columns: repeat(2, 10px);
    grid-gap: 1px;
    margin-left: 74px;
    color: var(--primaryText);
}
.thermo-indoor-span-row{
    grid-row: span 2/auto;
    margin-top: 0px;
    margin-bottom: 5px;
    font-size: 50px;
    font-weight: bold;
    color: var(--primaryText);
}
.thermo-indoor-5{
    margin-left: 58px;
    margin-top: 23px;
    font-size: 26px;
    font-weight: bold;
    color: var(--primaryText);
}
.thermo-temp_small{
    margin-left: 9px;
    font-size: 1px;
}

.fans {
    width: 220px;
    height: 80px;
    margin-right: 1px;
    margin-top: 1px;
}
.fan_text {
    width: 220px;
    height: 55px;
    margin-right: 2px;
    margin-top: 5px;
}

.fan_1 {
    width:55px;
    height:55px;
    margin-right: 2px;
    margin-top: 5px;
    -webkit-animation: rotation 1.75s infinite linear;
            animation: rotation 1.75s infinite linear;
    animationPlayState: paused;
}
.fan_2 {
    width: 26px;
    height: 26px;
    margin: 2px;
    margin-bottom: 12px;
    -webkit-animation: rotation 1.25s infinite linear;
            animation: rotation 1.25s infinite linear;
    animationPlayState: paused;
}
.fan_3 {
    width: 32px;
    height: 32px;
    margin: 2px;
    margin-bottom: 10px;
    -webkit-animation: rotation 1.5s infinite linear;
            animation: rotation 1.5s infinite linear;
    animationPlayState: paused;
}
.fan_4 {
    width: 40px;
    height: 40px;
    margin: 2px;
    margin-bottom: 8px;
    -webkit-animation: rotation 1.75s infinite linear;
            animation: rotation 1.75s infinite linear;
    animationPlayState: paused;
}
.fan_5 {
    width: 46px;
    height: 46px;
    margin: 1px;
    margin-bottom: 6px;
    -webkit-animation: rotation 1.8s infinite linear;
            animation: rotation 1.8s infinite linear;
    animationPlayState: paused;
}
.fan_text {
    margin: 3px;
    margin-right: 6px;
    font-size: 18px;
    font-weight: bold;
    color: var(--primaryText);
}

.rotate {
    -webkit-animation: rotation 2s infinite linear;
            animation: rotation 2s infinite linear;
}
@-webkit-keyframes rotation {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg);}
    to {-webkit-transform: rotate(359deg);transform: rotate(359deg);}
}
@keyframes rotation {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg);}
    to {-webkit-transform: rotate(359deg);transform: rotate(359deg);}
}

#drawing .fan {fill: var(--fans);}
#drawing .fanStroke {stroke: var(--fans);}

#drawing_2 .fan {fill: var(--fans_red);}

.indoor_label{
