.windows-container {
    display: grid;
    width: 230px;
    margin-left: -22px;
    margin-top: 15px;
    grid-template-areas: "windows-grid";
    border: 0px solid #db2b60;
    text-align: left;
}
.windows-grid {
    display: grid;
    grid-template-columns: 220px;
}
ul{
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
}
.window-li {
    position: relative;
