}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(8, 9, 96, 0.7);
}
.modal-main {
    position:fixed;
    top:45%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
.QR-image{
    border-radius: 0;
    width : 250px;
    height : 250px;
    cursor: help;
}
.QR-icon{
    float: left;
    border-radius: 0;
    padding-right: 14px;
