    width: 35px;
    height: 35px;
    cursor: help;
}

.display-block {
    display: block;
}
.display-none {
    display: none;
}

.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(8, 9, 96, 0.6);
}

.QR_background {fill: var(--QR_icon-backgound);}
.QR_icon {stroke: var(--QR_icons);}

